"use client";
import { useRef, useEffect } from 'react'
import Sidebar from '@/components/SideBar'
import { BackgroundGradientAnimation } from '@/components/ui/background-gradient-animation'
import RightPanel from '@/components/RightPanel'

export default function Home() {
  // 1️⃣ Create a ref for the right panel
  const rightPanelRef = useRef<HTMLDivElement>(null)

  // 2️⃣ Mirror window scroll into the right panel
  useEffect(() => {
    const syncScroll = () => {
      if (rightPanelRef.current) {
        rightPanelRef.current.scrollTop = window.scrollY
      }
    }
    window.addEventListener('scroll', syncScroll, { passive: true })
    return () => window.removeEventListener('scroll', syncScroll)
  }, [])

  return (
    <BackgroundGradientAnimation className="-z-10 pointer-events-none">
      <div className="grid md:grid-cols-[7.8fr_9fr] grid-cols-1 min-h-screen">
        
        {/* left panel */}
        <div className="md:sticky top-0 z-999">
          <Sidebar />
        </div>

        {/* right panel: attach ref here */}
        <div
          ref={rightPanelRef}
          className="fixed top-0 right-0 z-10 w-[300px] h-screen overflow-y-auto"
        >
          <RightPanel />
        </div>
      </div>
    </BackgroundGradientAnimation>
  )
}
